:root {
    @import url('https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');
    /* color */
    --bg-active: #f2f2f2;
    --bg-btn: #f8f8f8;
    --border-btn: #d5d5d5;

    /*  */
    --header-height: 56px;
    --sidebar-width: 250px;
}
* {
    padding: 0;
    margin: 0;
    list-style: none;
    box-sizing: border-box;
}
html {
    font-size: 62.5%;
}
body {
    font-size: 1.6rem;
    line-height: 1.5;
    font-family: 'Inter', sans-serif;
}
input {
    border: none;
    outline: none;
}
a {
    text-decoration: none;
    font-size: inherit;
    font-family: inherit;
    color: inherit;
}
button {
    background-color: transparent;
    border: none;
}
