.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    margin: 0 5px;
    cursor: pointer;
    &:hover {
        background-color: var(--bg-active);
    }
}
.icon {
    font-size: 1.8rem;
    font-weight: lighter;
}
