.wrapper {
    overflow: hidden;
    transition: scale linear 0.3s;
    border-radius: 10px;
    cursor: pointer;
    &:hover {
        padding: 10px;
        background-color: #fafafa;
        border: 0.5px solid #7777;
        box-shadow: 0 0 4px 1px #3333;
        z-index: 1;
        scale: 1.3;
    }
}
.group {
    display: flex;
    position: relative;
    width: 100%;
    margin-bottom: 10px;
}
.group2 {
    display: flex;
    margin-bottom: 10px;
}
.img {
    width: inherit;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}
.img-infor {
    position: absolute;
    left: 20px;
    top: 20px;
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.detail {
    margin-left: 10px;
}
.title {
}
.text {
    font-size: 1.3rem;
    color: #aaaa;
}
.nickname {
    font-size: 1.3rem;
    &:hover {
        text-decoration: underline;
    }
}
