.wrapper {
    background-color: white;
    padding: 10px 0;
    z-index: 10;
    border-radius: 6px;
    border: 1px solid #aaa;
    box-shadow: 0px 0px 5px 2px #eeee;
    max-height: 80vh;
    overflow-y: scroll;
}

.group {
    margin: 10px 0;
    + .group {
        padding-top: 10px;
        border-top: 1px solid #cccc;
    }
}
.header {
    padding: 6px 10px;
    display: flex;
    align-items: center;
    .title {
        position: relative;
        left: 50%;
        transform: translateX(-70%);
    }
}
.item {
    position: relative;
    padding: 8px 10px;
    min-width: 260px;
    display: flex;
    align-items: center;

    .icon {
        margin: 0 16px;
        width: 20px;
    }
    .name {
        font-size: 1.5rem;
    }
    .icon-right {
        position: absolute;
        left: 90%;
        font-size: 1.3rem;
    }
    .hide {
        color: white;
    }
    &:hover {
        background-color: var(--bg-active);
        .hide {
            color: var(--bg-active);
        }
    }
}
