.wrapper {
    min-width: 40vw;
    display: flex;
    align-items: center;
}
.tippy {
    flex: 1;
}
.type {
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 50px 0 0 50px;
    &:focus-within {
        border: 0.5px solid var(--border-btn);
        .infor {
            color: black;
        }
    }
}
.infor {
    color: white;
    margin-left: 20px;
}
.input {
    flex: 1;
    font-size: 1.8rem;
    font-weight: 200;
    line-height: 2;
    padding: 0 18px;
    border: 0.5px solid var(--border-btn);
    border-radius: 50px 0 0 50px;
    &:focus {
        border: none;
    }
}
.keyboard {
    position: absolute;
    left: calc(100% - 30px);
}
.infor,
.keyboard,
.search {
    font-size: 1.6rem;
}
.search {
    border: 0.5px solid var(--border-btn);
    border-left: none;
    outline: none;
    border-radius: 0 50px 50px 0;
    background-color: var(--bg-btn);
    padding: 10px 20px;
    cursor: pointer;
    &:hover {
        background-color: var(--bg-active);
    }
}

.result-list {
    width: 100%;
    min-width: 450px;
    padding: 20px 0 10px 0;
    border: 1px solid var(--bg-active);
    border-radius: 8px;
    box-shadow: 1px 1px 1px 1px var(--bg-btn);
    background-color: white;
}
.item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3px 10px;
    &:hover {
        background-color: var(--bg-active);
    }
    .icon {
        padding: 2px 10px;
    }
    .title {
        flex: 1;
        font-size: 1.8rem;
    }
    .delete {
        padding: 2px 5px;
        font-size: 1.4rem;
        color: #36c;
        &:hover {
            text-decoration: underline;
        }
    }
}
