.wrapper {
    position: relative;
    padding: 20px 0;
}
.select {
    position: fixed;
    top: var(--header-height);
    width: 95vw;
    padding: 10px;
    background-color: white;
    white-space: nowrap;
}

.container {
    margin-top: 30px;
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(248px, 1fr));
    // grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
}
