.nav-item {
    display: flex;
    align-items: center;
    padding: 6px 0px;
    border-radius: 8px;
    &:hover {
        background-color: var(--bg-active);
    }
    .nav-icon,
    .nav-title {
        font-size: 1.6rem;
        font-weight: lighter;
        color: inherit;
    }
    .icon-pad {
        height: 30px;
        width: 30px;
        border-radius: 50%;
        margin: 0 10px 0 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .nav-icon {
        width: 20px;
    }
}

.icon-red {
    .icon-pad {
        background-color: red;
    }
    .nav-icon {
        color: aliceblue;
    }
}
.icon-green {
    .icon-pad {
        background-color: greenyellow;
    }
    .nav-icon {
        color: rgb(0, 93, 175);
    }
}

.active {
    background-color: var(--bg-active);
    font-weight: bold;
}

.short-menu {
    flex-direction: column;
    padding: 20px 4px;
    .nav-icon {
        margin: auto;
    }
    .nav-title {
        font-size: 1.1rem;
        margin-top: 10px;
        letter-spacing: -0.5px;
    }
}
