.btn {
    padding: 8px 14px;
    background-color: #f2f2f2;
    cursor: pointer;
    border-radius: 6px;
    color: #0f0f0f;
    &:hover {
        background-color: #e2e2e2;
    }
    + .btn {
        margin-left: 10px;
    }
}

.active {
    background-color: #0f0f0f;
    color: white;
    &:hover {
        background-color: #0f0f0f;
    }
}
